* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  /* background-color: white; */
  background-color: #fff;

  color: #000;
}

a{
  text-decoration: none
}

body {
  min-height: 100vh;
  font-size: 1.5rem;
  background-color: #fff;
}

input,
textarea,
button,
select {
  font: inherit;
  margin-bottom: 1em;
}

header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: darkslateblue;
  color: darkgray;
  position: sticky;
  top: 0;
  z-index: 10;
}

.header-logo{
  margin-right: 20px;
}

header h1{
  padding-left: 5%;
}
header nav{
  align-self: flex-end;
}

nav {
  display: flex;
  justify-content: flex-end;
}

nav ul {
  list-style-type: none;
}
nav ul {
  margin-bottom: 0;
  padding-left: 0;
}

nav ul li {
  display: inline-block;
  margin-right: 3rem;
  height: 45px;
}

nav a, nav a:visited {
  color: black;
  text-decoration: none;
  font-weight: bold;
}

nav a:hover, nav a:focus {
  text-decoration: underline;
}

main {
  max-width: 500px;
  margin: auto;
}

section {
  margin-top: 1em;
}

article {
  margin: 0.5em;
  /* border: 1px solid #000; */
  border-radius: 10px;
  padding: 1em;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  margin-bottom: 1rem;
}

p {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  font-size: 1.2rem;
  margin: 0.5em 0;
  overflow: hidden;
}

form {
  display: flex;
  flex-direction: column;
}

textarea {
  height: 200px;
}

.postCredit {
  font-size: 1rem;
}

.postCredit a, 
.postCredit a:visited {
  margin-right: 0.5rem;
  color: black;
}

.postCredit a:hover, 
.postCredit a:focus {
  color: hsla(0, 0%, 0%, 0.75);
}

.excerpt {
  font-style: italic;
  overflow: hidden;
}

.reactionButton {
  display: none;
  margin: 0 0.25em 0 0;
  background: transparent;
  border: none;
  color: #000;
  font-size: 25px;
}
.reactionButton:first-child{
  display: initial;
}

.reactions-container:hover .reactionButton{
  display: initial;
}



/* .reactions-container{
  position: relative;
} */
.post-bottom-options-container span{
  position: absolute;
  right: 20px;
  bottom: 23px;
}
.post-bottom-options-container span i{
  padding-right: 10px;
}

.deleteButton {
  background-color: palevioletred;
  color: white;
}

.btn{
  color: darkgray;
  font-size: 20px;
}

.btn-1{
}
.btn-2{
  background-color: blueviolet;
}
.btn-3{
  background-color: gray;
}

article{
  position: relative;
  background-color: #e4e6eb;
  height: 550px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 50px;
  box-shadow: 1px 1px 6px 0px gray;
}
article h2 ,.excerpt , .postCredit ,.div{
  width: 100%;
}
article .excerpt{
  
}
article .postCredit{
  display: flex;
  align-items: flex-end;
  /* margin-top: 73px; */
  margin-bottom: -70px;
  justify-content: space-between;
}
article div{
  align-self: flex-end;
}

article h2 .profile-icon{
  padding-top: 4px;
  padding-left: 7px;
  margin-right: 20px;
  border: 1px solid black;
  width: 44px;
  height: 44px;
  border-radius: 22px;
  background-color: black;
}
article h2 .options-icon{    
  position: absolute;
  right: 25px;
}
article h2 button{
  position: relative;
  top: 30px;
  right: 28px;
  font-size: 18px !important;

}

article h2 .post-title{
  position: absolute;
  left: 82px;
  top: 20px;
}

.post-bottom-options-container .reactions-number{
  position: absolute;
  left: 20px;
  bottom: 0px;
  font-size: 20px;
}
.reactions-number .reaction-1 ,.reaction-2 ,.reaction-3 ,.reaction-4{
  position: relative;
  bottom: 0;
}
.reactions-number .reaction-1{
  left:  10px;
}
.reactions-number .reaction-2{
  right:  15px;
}
.reactions-number .reaction-3{
  right:  39px;
}
.reactions-number .reaction-4{
  right:  66px;
}

.postCredit::after{
  position: absolute;
  content: '';
  right: 17px;
  width: 437px;
  height: 0.5px;
  color: black;
  background-color: darkgray;
}
article h2::after{
  position: absolute;
  content: '';
  right: 0px;
  top: 100px;
  width: 100%;
  height: 0.5px;
  /* color: black; */
  background-color: gray;
}

.options-block{
  display: none;
  border-radius: 15px;
  font-size: 20px;
  background-color: #e4e6eb;
  border: 1px solid black;
  position: absolute;
  right: -115px;
}
.options-block ul{
  list-style: none;
  padding-right: 25px;
  /* margin: 0; */
}
.options-block ul li{
  width: 100%;
  border-bottom: 1px solid gray;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 0;
  padding-right: 0;
  border-radius: 5px;
  /* padding: 5px 5px; */
}
.options-block ul li:hover{
  background-color: #ccd4ea;
}
.options-block a{
  color: unset;
}

.options-block ul li i {
  font-size: 15px;
  border: unset;
  background-color: unset;
  margin-right: 20px;
}

.options-icon-container:hover .options-block{
  display: initial;
}

@media (max-width: 600px) {
  header h1{
    font-size: 30px;
    margin-bottom: 20px;
  }
  header{
    height: unset;
    display: block;
    padding-bottom: 0;
    margin-bottom: 0;
  }
  
  header nav  ul li .btn{
    font-size: 12px;
  }

  article h2 .profile-icon {
    padding-top: 7px;
    padding-left: 12px;
    margin-right: 20px;
    width: 50px;
    font-size: 30px;
    height: 50px;
    border-radius: 25px;
  }
  article h2 .post-title{
    position: absolute;
    left: 83px;
    top: 28px;
  }
  .create-post-container ,.edit-post-container ,.authors-container{
    width: 80%;
    margin-left: 10%;
  }
  .options-block{
    
    right: 20px;
    z-index: 5;
  }
}